.tabs {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.tabs__wrapper {
  position: relative;
  width: 100%;
  z-index: 1;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.tabs__element {
  width: 100%;
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

.tabs__element .for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

.tabs__element .is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.tabs__buttons {
