    margin: 0 auto;
    margin-bottom: 35px;
    width: 100%;
  }
}
.tabs__buttons::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  width: 100%;
  background-color: rgba(120, 120, 120, 0.2);
}

.tabs__button {
  border: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
